import React, { Component } from "react";
import "react-calendar/dist/Calendar.css";
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";
import config from "../../constant/config.js";
import "react-tagsinput/react-tagsinput.css";
import {
  NotificationContainer,
  NotificationManager,
} from "react-notifications";
const axios = require("axios");

class AddUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      nameError: false
    };
  }
  componentDidMount() {
    this.fetchusersdata();
  }

  fetchusersdata() {
    let self = this;
    axios
      .get(config.baseurl + "/users/allusers?page=1")
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ userValues: response.data.rows });
        }
      });
  }

  handleusername(e) {
    this.setState({ nameError: false });
    if (this.state.userValues.length <= 0) {
      this.setState({
        username:
          e.target.value.charAt(0).toUpperCase() + e.target.value.slice(1),
        error: "",
      });
    } else {
      this.state.userValues.filter((data) => {
        if (data.username.toUpperCase() !== e.target.value.toUpperCase()) {
          this.setState({
            username:
              e.target.value.charAt(0).toUpperCase() + e.target.value.slice(1),
            error: "",
          });
        } else {
          this.setState({ nameError: true }, () => {});
          NotificationManager.listNotify = [];
          NotificationManager.error("User already exits!");
        }
      });
    }
  }
  handlepassword(e) {
    this.setState({
      password: e.target.value,
    });
  }
  formDataSubmit(e) {
    e.preventDefault();
    let Data = {
      username: this.state.username,
      password: this.state.password,
    };
    axios
      .post(config.baseurl + "/users/create", Data)
      .then(function (response) {
        if (response.status == 200) {
          this.props.onHide();
        }
      })
      .catch(function (error) {});
  }
  crossClick(e) {
    e.preventDefault();
    this.props.onHide();
  }

  render() {
    return (
      <>
        <NotificationContainer />
        <div className="content">
          <Grid fluid>
            <div className="symbol-box">
              <div className="cross_div">
                <a onClick={(e) => this.crossClick(e)}>
                  <i className="fa fa-times" aria-hidden="true"></i>
                </a>
              </div>
              <div className="detail-text">
                <h3> Add User </h3>
              </div>
              {/* <div className="terminal-text">
                <label className="pnameadjust"> Profile Name </label>
                <input type="text" name="profilename" className="Itext" placeholder="Profile Name" onChange={(e) => this.handleprofileName(e)} />
              </div> */}

              <form>
                <div className="terminal-text">
                  <p className="profilename pnameadjust"> Username</p>
                  <input
                    type="text"
                    name="username"
                    value={this.state.username}
                    className="Itext"
                    onChange={(e) => this.handleusername(e)}
                  />
                </div>
                <div className="terminal-text">
                  <p className="profilename pnameadjust"> Password</p>
                  <input
                    type="password"
                    name="password"
                    value={this.state.password}
                    className="Itext"
                    onChange={(e) => this.handlepassword(e)}
                  />
                </div>
                <div className="save_button_filter">
                  <div className="savebtn">
                    <Button
                      className="btn-primary savebutton"
                      variant="primary"
                      size="lg"
                      block
                      onClick={(e) => this.formDataSubmit(e)}
                    >
                      <b>Save</b>
                    </Button>
                  </div>
                </div>
              </form>
            </div>
          </Grid>
        </div>
      </>
    );
  }
}

export default AddUser;
