import React, { Component } from "react";
import "react-calendar/dist/Calendar.css";
import "react-datepicker/dist/react-datepicker.css";
import { Grid, Row, Col, Button } from "react-bootstrap";

class UserDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      loggedIn: "",
    };
  }
  crossClick(e) {
    e.preventDefault();
    this.props.onHide();
  }

  componentDidMount() {
    let data = this.props.data;
    this.setState({
      username: data.username,
      loggedIn: data.logged_in,
    });
  }

  render() {
    return (
      <div className="content">
        <Grid fluid>
          <div className="symbol-box">
            <div className="cross_div">
              <a onClick={(e) => this.crossClick(e)}>
                <i className="fa fa-times" aria-hidden="true"></i>
              </a>
            </div>
            <div className="detail-text">
              <h3> User Details </h3>
            </div>
            <Row>
              <Col md={12}>
                <div className="card">
                  <table className="table mt20">
                    <tbody>
                      <tr>
                        <td className="b-clr">
                          <span>Username :</span>
                        </td>

                        <td
                          className="b-clr text-center"
                          style={{ width: "40%" }}
                        >
                          {this.state.username}
                        </td>
                      </tr>
                      <tr>
                        <td className="b-clr ">
                          <span>Logged In : </span>
                        </td>
                        <td
                          className="b-clr text-center"
                          style={{ width: "40%" }}
                        >
                          {this.state.loggedIn === 1 ? "Yes" : "No"}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </Col>
            </Row>
          </div>
        </Grid>
      </div>
    );
  }
}

export default UserDetail;
