import React, { Component } from "react";
import "react-calendar/dist/Calendar.css";
import { Grid, Row, Col, Modal } from "react-bootstrap";
import "react-datepicker/dist/react-datepicker.css";
import config from "../../constant/config.js";
import {
  NotificationContainer,
  NotificationManager,
} from "react-notifications";
import "react-notifications/lib/notifications.css";
import { api } from "api";
import ReactPaginate from "react-paginate";
import UserDetail from "./userdetail";
import UserEdit from "./useredit";
import AddUser from "./adduser";

const axios = require("axios");

class Users extends Component {
  constructor(props) {
    super(props);
    this.state = {
      addUser: false,
      filter: true,
      usersData: [],
      deleteData: [],
      editData: [],
      showDelete: false,
      showEdit: false,
      detailForm: false,
      detailData: [],
      count: "",
      pages: "",
      page: 1,
    };
  }
  fetchusersdata() {
    let self = this;
    axios
      .get(config.baseurl + `/users/allusers?page=${self.state.page}`)
      .then(function (response) {
        if (response.status == 200) {
            console.log(response.data, 'users')
          self.setState({
            usersData: response.data.rows,
            count: response.data.count,
            pages: response.data.pages,
          });
        }
      });
  }
  handlePageClick = (data) => {
    this.setState({ page: data.selected + 1 }, () => {
      this.fectheAllAccount(this.state.page);
    });
  };
  showaddUser = () => {
    this.setState({ addUser: true, filter: false });
  };
  componentDidMount() {
    this.fetchusersdata();
  }

  editUser = (e, data) => {
    e.preventDefault();
    this.setState({ showEdit: true, editData: data });
  };

  deleteUser(e, data) {
    e.preventDefault();
    this.setState({ deleteData: data, showDelete: true });
  }
  deletepopupClick(e) {
    e.preventDefault();
    let Data = {
      id: this.state.deleteData.id,
    };
    let self = this;
    axios
      .post(config.baseurl + "/users/delete", Data)
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ showDelete: false });
          self.fetchusersdata();
        }
      });
  }
  deletepopupclose(e) {
    e.preventDefault();
    this.setState({ showDelete: false });
  }
  viewUser(e, data) {
    e.preventDefault();

    this.setState({ detailForm: true, detailData: data });
  }

  createTr = () => {
    return (
      this.state.usersData.length > 0 &&
      this.state.usersData.map((item, index) => {
        return (
          <tr key={index}>
            <th scope="row">{index + 1}</th>
            <td>{item.username}</td>
            <td>{item.logged_in}</td>
            <td>
              <span onClick={(e) => this.viewUser(e, item)}>
                <i className="pointer fa fa-eye facolor" aria-hidden="true"></i>
              </span>
              <span onClick={(e) => this.editUser(e, item)}>
                <i
                  className="pointer fa fa-pencil-square-o facolor"
                  aria-hidden="true"
                ></i>
              </span>
                <span onClick={(e) => this.deleteUser(e, item)}>
                  <i
                    className="pointer fa fa-trash facolor"
                    aria-hidden="true"
                  ></i>
                </span>
            </td>
          </tr>
        );
      })
    );
  };

  render() {
    return (
      <>
        <NotificationContainer />
        <div className="content">
          {this.state.detailForm ? (
            <UserDetail
              data={this.state.detailData}
              show={this.state.detailForm}
              onHide={() => this.setState({ detailForm: false })}
            />
          ) : (
            <>
              {this.state.showEdit ? (
                <UserEdit
                  data={this.state.editData}
                  show={this.state.showEdit}
                  onHide={() =>
                    this.setState({ showEdit: false }, () =>
                      this.fetchusersdata()
                    )
                  }
                />
              ) : (
                <>
                  {!this.state.addUser && (
                    <Grid fluid>
                      <div className="selection-box">
                        <span className="terminal-text"> Users </span>
                      </div>
                      <button
                        className="pull-right filter-add-button"
                        onClick={this.showaddUser}
                      >
                        Add New
                      </button>
                    </Grid>
                  )}
                  {
                    this.state.addUser ? (
                      <AddUser
                        show={this.state.addUser}
                        onHide={() =>
                          this.setState({ addUser: false }, () => {
                            this.fetchusersdata();
                          })
                        }
                      />
                    ) : (
                      <div style={{ background: "#ffffff" }}>
                        <Row>
                          <Col md={12}>
                            <div style={{ padding: "10px" }}>
                              <table className="table table-striped table-bordered">
                                <thead>
                                  <tr>
                                    <th scope="col">Sr.No.</th>
                                    <th scope="col">User Name</th>
                                    <th scope="col">Logged In</th>
                                    <th scope="col">Action</th>
                                  </tr>
                                </thead>
                                <tbody>
                                    {this.createTr()}
                                </tbody>
                              </table>
                              <ReactPaginate
                                previousLabel={"prev"}
                                nextLabel={"next"}
                                breakLabel={"..."}
                                pageCount={this.state.pages}
                                marginPagesDisplayed={9}
                                pageRangeDisplayed={9}
                                onPageChange={(e) => this.handlePageClick(e)}
                                containerClassName={"pagination"}
                                subContainerClassName={"pages pagination"}
                                activeClassName={"active"}
                              />
                            </div>
                          </Col>
                        </Row>
                      </div>
                    )
                  }

                  {this.state.showDelete && (
                    <div className="delete_popup">
                      <h3>Are you sure ?</h3>
                      <span
                        className="pointer dltbtn"
                        onClick={(e) => this.deletepopupClick(e)}
                      >
                        Delete
                      </span>
                      <span
                        className="pointer closebtn"
                        onClick={(e) => this.deletepopupclose(e)}
                      >
                        Close
                      </span>
                    </div>
                  )}
                </>
              )}
            </>
          )}
        </div>
      </>
    );
  }
}

export default Users;
